<template>
  <view :class="['type-tag', getClass]">
    <text>{{ rank }}</text>
  </view>
</template>

<script>
export default {
  name: 'RankIcon',
  props: {
    rank: {
      type: [Number, String],
      default: '',
    },
  },
  data() {
    return {}
  },
  computed: {
    getClass() {
      if (this.rank < 4) {
        return 'rank_' + this.rank
      } else {
        return 'rank_4'
      }
    },
  },
  methods: {},
}
</script>

<style scoped lang="scss">
.type-tag {
  height: 64rpx;
  width: 50rpx;
  background-image: url('@/static/images/rank_4.png');
  background-size: 100% 100%;
  background-repeat: no-repeat;
  text-align: center;
  padding-top: 24rpx;
  box-sizing: border-box;
  color: #333;
  font-size: 24rpx;
}
.rank_1 {
  background-image: url('@/static/images/rank_1.png');
  color: #e87e2c;
}
.rank_2 {
  background-image: url('@/static/images/rank_2.png');
  color: #5173f0;
}
.rank_3 {
  background-image: url('@/static/images/rank_3.png');
  color: #c86c29;
}
</style>
